import AppModel from './AppModel';
import AppView from '../views/AppView';

export default class App {
    constructor(value) {
        this.slider = document.querySelector('.slider');
        this.cards = null;
        this.value = value;
        this.key = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y';
        this.state = {
            url: `https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=15&q=${this.value}`,
        };
        this.next = null;
        this.slider.addEventListener('scroll', this.continueScroll.bind(this), false);
    }

    async start() {
        const model = new AppModel(this.state);
        this.next = await model.getNextPage();
        const id = await model.getId();
        this.state.url2 = `https://www.googleapis.com/youtube/v3/videos?key=${this.key}&id=${id.join(',')}&part=snippet,statistics`;
        const data = await model.getInfo();
        const view = new AppView(data);
        view.render();
    }

    async continueScroll() {
        const scroll = this.slider.scrollLeft;
        const numberCard = getComputedStyle(document.body).getPropertyValue('--card');
        const width = parseInt(getComputedStyle(document.querySelector('.card')).getPropertyValue('width'), 10);
        const margin = parseInt(getComputedStyle(document.querySelector('.card')).getPropertyValue('margin-right'), 10);
        this.cards = document.getElementsByClassName('card').length - numberCard;

        const widthOfAllCards = (width - margin) * this.cards;
        const widthOfAllCardsWithoutTabs = widthOfAllCards - margin;

        if (scroll <= widthOfAllCards && scroll >= widthOfAllCardsWithoutTabs) {
            this.state.url = `https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=15&q=${
                this.value
            }&pageToken=${this.next}`;
            this.start();
        }
    }
}
